<template>
   <div class="page-body">
    <div class="page-header">
      <!--   <h1 class="page-title">Table表格数据</h1> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>人员管理</el-breadcrumb-item>
         <el-breadcrumb-item>授权</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
 
    <div class="box" >
    	<el-table 
    :data="tableData"
    style="width:1000px;margin-bottom: 20px;"
    row-key="id"
    border
    default-expand-all
    :tree-props="{children: '2', hasChildren: 'hasChildren'}">
     <el-table-column type="selection" width="55"> </el-table-column> 
     <el-table-column v-for="(item,index) in menu" :prop="item.name" label="角色菜单" width="180"> </el-table-column>
    <!--   <el-table v-for="(item,index) in menu" :key="index"></el-table> -->
  </el-table>
     
  </div>
  </div>
</template>

<script>
export default{
	data(){
	  return {
	  	menu:[],
	  	tableData: []
	  }
	},

    created(){
    	this.roleMenu()
    },

	methods:{
		roleMenu(){
			const id=this.$route.query.id
		   const head = { headers: { "api-token": this.$sign(), "Authorization": this.http.vtoken } };
          this.http.axios.get(`${this.http.role}getMenu?id=${id}`, head).then(res=>{
          	console.log(res.data.data);
          	
          })
			
		}
	}

}

</script>

<style scoped>

</style>


